import store from 'store'

class Token {
  static $token = store.get('Token')

  set Token (token) {
    store.set('Token', token)
    Token.$token = token
  }

  get Token () {
    return Token.$token
  }

  clearToken () {
    Token.$token = null
    store.remove('Token')
    return null
  }
}

class Search {
  static $history = store.get('SearchHistory') || []

  set History (token) {
    store.set('SearchHistory', token)
    Search.$history = token
  }

  get History () {
    return Search.$history
  }

  clearHistory () {
    Search.$history = []
    store.remove('SearchHistory')
    return null
  }
}

class Banner {
  static $banner = store.get('BannerDetails')

  set banner (token) {
    store.set('BannerDetails', token)
    Banner.$banner = token
  }

  get banner () {
    return Banner.$banner
  }

  clearBanner () {
    Banner.$banner = null
    store.remove('BannerDetails')
    return null
  }
}
const token = new Token()

const searchHistory = new Search()
const banner = new Banner()
export {
  banner,
  token,
  searchHistory
}
