<template>
  <div>
    <nav-head title="订单" :hideBack="false" FixedOrTransparency="NoTransparency isFixed"/>
    <!--<bet-scheme></bet-scheme>-->
    <div style="height: 50px;overflow: hidden;">
      <div class="nav-button">
        <div :class="'state-'+state">
          <p @click="switchState(0)">全部</p>
          <p @click="switchState(1,0)">待开奖</p>
          <p @click="switchState(2,1)">中奖</p>
          <p @click="switchState(3,-1)">未中奖</p>
        </div>
      </div>
    </div>
    <Loadmore :top-method="loadTop" ref="loadmore">
      <div id="information"
           v-infinite-scroll="loadMore"
           infinite-scroll-disabled="loading"
           infinite-scroll-distance="10"
           infinite-scroll-immediate-check="true">
        <ul :style="{minHeight: clientHeight+'px'}" style="overflow: hidden">
          <no-content v-if="orderStatus[state]&&orderList[state].length===0" tag="li" img="2" :text="['没有订单信息!','赶紧取下注吧!']"/>
          <order-list-entry v-for="(n,i) in orderList[state]" :key="`${i}${n.order_id}`" :item="n"/>
        </ul>
      </div>
    </Loadmore>
  </div>
</template>

<script>//
import TheGameSwitch from '../../../components/TheGameSwitch'
import BetScheme from '../../../components/BetScheme'
import OrderListEntry from '../../../components/365/OrderListEntry'
import http from '../../../tool/http'
import { copy } from '../../../tool/util'
import { mapState } from 'vuex'
import Loadmore from '../../../components/loadmore'

export default {
  name: 'scheme365',
  data () {
    return {
      Prohibit: false,
      state: 0,
      orderList: [[], [], [], []],
      orderStatus: [false, false, false, false]
    }
  },
  computed: {
    ...mapState({
      clientHeight: state => state.mine.Device.height - 46 - 49 - 50
    }),
    loading () {
      return this.orderStatus[this.state]
    }
  },
  methods: {
    status () {
      return this.state === 1 ? 0 : this.state === 2 ? 1 : this.state === 3 ? -1 : null
    },
    loadTop () {
      // 下拉刷新
      this.getData(this.state, this.status()).then(() => {
        setTimeout(() => {
          this.$refs.loadmore && this.$refs.loadmore.onTopLoaded()
        }, 500)
      })
    },
    loadMore () {
      // 上拉加载
      if (this.Prohibit && this.orderStatus[this.state] === false) {
        this.Prohibit = false
        http.getUserBetOrder(this.status(), this.orderList[this.state].length / 20 + 1).then(value => {
          if (value && value.length) {
            const list = copy(this.orderList[this.state])
            list.push(...value)
            this.$set(this.orderList, this.state, list)
          }
          this.$set(this.orderStatus, this.state, !value || value.length === 0 || (value.length % 10 !== 0))
          this.Prohibit = true
        })
        console.log(0)
      }
    },
    switchState (state, type) {
      if (this.orderStatus[state] === false && this.orderList[state].length === 0) {
        this.getData(state, type).then(() => {
          this.state = state
        })
      } else {
        this.state = state
      }
    },
    getData (state, type) {
      return http.getUserBetOrder(type, 0).then(value => {
        if (value && value.length) {
          this.$set(this.orderList, state, value)
        }
        this.$set(this.orderStatus, state, !value || value.length === 0 || (value.length % 10 !== 0))
      })
    }
  },
  created () {
    this.getData(0).then(() => {
      this.Prohibit = true
    })
  },
  components: {
    Loadmore,
    OrderListEntry,
    BetScheme,
    TheGameSwitch
  }
}
</script>

