  .nav-button {
    position: fixed;
    width: 100%;
    top: 46px;
    z-index: 2;
    max-width: 640px;
    background-color: #131313;
    padding: 0 10px;
    > div {
      background-color: #1C1C1C;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      height: 30px;
      border-radius: 4px;
      margin: 10px 0;
      &.state-0 p:nth-child(1), &.state-1 p:nth-child(2), &.state-2 p:nth-child(3), &.state-3 p:nth-child(4) {
        color: #FFC63A;
        font-weight: bold;
      }
      p {
        flex: 1;
        text-align: center;
        font-size: 14px;
