<template>
  <router-link
    class="order-list-entry"
    :to="{path:'/bettingDetails',query:{
    id:item.order_id,
    distribute_status:item.distribute_status,
    winnings_status:item.winnings_status
  }}"
    tag="li">
    <img :src="imgSrc" width="30" height="30" alt="">
    <div>
      <div>
        <p style="color:white ">{{ typeName }}</p>
        <p :class="status"><span v-show="status==='state-0'"> {{ item.winnings_bonus }}</span></p>
      </div>
      <div>
        <p>{{ time }}</p>
        <p>已投:{{ item.total_amount }}</p>
      </div>
    </div>
    <img src="../../assets/store/365/ic_go.png" width="20" height="20" alt="">
  </router-link>
</template>

<script>//
import { timeFmt } from '../../tool/util'

export default {
  name: 'OrderListEntry',
  props: {
    item: { type: Object }
  },
  computed: {
    imgSrc () {
      if (this.item.play_type === 1) {
        return require(`../../assets/store/365/ic_zaopan.png`)
      }
      return require(`../../assets/store/365/ic_guoguan.png`)
    },
    typeName () {
      if (this.item.play_type === 1) {
        return '早盘'
      }
      return '过关'
    },
    time () {
      return timeFmt(this.item.create_time, 'yyyy-MM-dd hh:mm')
    },
    status () {
      const w = this.item.winnings_status
      const d = this.item.distribute_status
      const t = this.item.action_type
      // d派奖状态
      // 开奖状态
      if (d === 0 && w === 0) {
        return 'state-2'
      } else if (d === 1 && w === 0) {
        return 'state-1'
      } else if (d === 1 && w === 1) {
        if (t === 1) {
          return 'state-3'
        }
        return 'state-0'
      } else if (w === 1) {
        if (t === 1) {
          return 'state-3'
        }
        return 'state-0'
      } else if (w === -1) {
        return 'state-1'
      } else {
        return 'state-2'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .order-list-entry {
    margin: 5px 10px;
    border-radius: 4px;
    background-color: #313131;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    padding-left: 10px;
    > div:nth-of-type(1) {
      flex: 1;
      margin: 0 10px;
      > div {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-between;
        height: 30px;
        p {
          font-size: 12px;
          color: #999999;

          &.state-0, &.state-0:before {
            content: '中奖';
            color: #FFC63A;
          }
          &.state-1:before {
            content: '未中奖';
          }
          &.state-2:before {
            content: '待开奖';
            color: #ffffff;
          }
          &.state-3, &.state-3:before {
            content: '取消';
            color: #FF3333;
          }
          &:last-child {
            text-align: right;
            flex: 1;
          }
        }
      }
      > div:nth-child(1) {
        border-bottom: 1px solid #494949;
      }
    }
  }
</style>
