<template>
  <div class="nav-switch">
    <div @click="showRotate"><span>{{ PatternName }}</span><span :class="rotate"></span></div>
    <ul v-show="rotate">
      <li v-for="(n,i) in Pattern" :key="n.id" @click="SwitchIndex(i)"><span :class="{activation:i===PatternIndex}">{{ n.play_name }}</span>
      </li>
    </ul>
  </div>
</template>

<script>//
import { mapActions, mapState } from 'vuex'
import { GET_365_HOME_DATA } from '../../vuex/365/home'

export default {
  name: 'NavSwitch',
  data () {
    return {
      rotate: ''
    }
  },
  computed: {
    ...mapState({
      PatternIndex: state => state.home.PatternIndex,
      Pattern: state => state.home.Pattern
    }),
    PatternName () {
      if (this.Pattern[this.PatternIndex] && this.Pattern[this.PatternIndex].play_name) {
        return this.Pattern[this.PatternIndex].play_name
      }
      return '加载中..'
    }
  },
  methods: {
    ...mapActions({
      getHomeData: GET_365_HOME_DATA
    }),
    showRotate () {
      this.rotate = this.rotate ? '' : 'rotate180'
    },
    SwitchIndex (index) {
      if (index !== this.PatternIndex) {
        this.getHomeData({ type: 1, index1: index, index2: 0, index3: 0 }).then(() => {
          this.$emit('change')
        })
      }
      this.showRotate()
    }
  }
}
</script>

<style scoped lang="scss">
  .nav-switch {
    background-color: $background-color;
    > div {
      padding: 13px 0;
      text-align: center;
      color: #FFC63A;
      font-size: 15px;
      line-height: 1;
      span {
        vertical-align: middle;
      }
      span:last-child {
        line-height: 1;
        display: inline-block;
        margin: 0 10px;
        width: 0;
        height: 0;
        border-width: 9px 6px 0;
        border-style: solid;
        border-color: #FFC63A transparent transparent; /*透明 透明  灰*/
        transform: rotate(0);
        transform-origin: center center;
        transition: transform 1s;
      }
      span:last-child.rotate180 {
        transform: rotate(-180deg);
      }
    }
    > ul {
      display: flex;
      flex-flow: row wrap;
      justify-content: flex-start;
      padding-top: 10px;
      margin: 0 14px;
      > li {
        width: 33.3%;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        margin-bottom: 10px;
        span {
          line-height: 1;
          padding: 7px 25px;
          font-size: 12px;
          color: #999999;
          border: 1px solid rgba(153, 153, 153, 1);
          border-radius: 4px;
        }
        span.activation {
          color: white;
          border-color: #FFC63A;
        }
      }
    }
  }
</style>
