<template>
  <div class="Bets-Item" :class="'state-'+initStatus">
    <div class="top" @click="open">
      <div><img :src="item.icon" width="20" alt=""><span>{{ item.game_name }}</span></div>
      <div><img :src="item.host_team_logo" width="26" alt=""></div>
      <div><img :src="item.guest_team_logo" width="26" alt=""></div>
    </div>
    <div class="center" @click="open">
      <p><span>{{ item.game_start_time|dateFormat('hh:mm') }}</span><span>{{ item.match_type }}</span></p>
      <div><span>{{ item.host_team_name }}</span></div>
      <div><span>{{ item.guest_team_name }}</span></div>
    </div>
    <div class="bottom" :class="betNow">
      <div :class="{activation:initStatus===0}"><span>{{ status }}</span></div>
      <div @click="betting(0)"><span>{{ hostOdds }}</span></div>
      <div @click="betting(1)"><span>{{ guestOdds }}</span></div>
    </div>
  </div>
</template>

<script>//
import { score } from '../../tool/filter'
import { mapState } from 'vuex'
import { initStatus, oddsFiltration } from '../../tool/util'
import { token } from '../../tool/storeManage'
import { Toast } from 'mint-ui'

export default {
  name: 'BetsItem',
  props: {
    item: { type: Object },
    aims: { type: Array }
  },
  computed: {
    ...mapState({
      Pattern: state => state.home.Pattern[state.home.PatternIndex] || {},
      Betting: state => state.home.PassStatus.Betting,
      MatchStatus: state => state.home.MatchStatus
    }),
    betNow () {
      let className = ''
      if (this.Betting[this.item.smid]) {
        if (this.Betting[this.item.smid][`${this.item.smpid}0`]) {
          className += 'betting-0 '
        }
        if (this.Betting[this.item.smid][`${this.item.smpid}1`]) {
          className += 'betting-1 '
        }
      }
      return className
    },
    initStatus () {
      return initStatus(this.item.status, this.item.game_start_time)
    },
    hostOdds () {
      if (this.initStatus === 0) {
        return oddsFiltration(this.item.odds, true)
      }
      return score(this.item.sports_match_score, true)
    },
    guestOdds () {
      if (this.initStatus === 0) {
        return oddsFiltration(this.item.odds, false)
      }
      return score(this.item.sports_match_score, false)
    },
    status () {
      // if (this.item.prize_status > 0 && this.item.prize_result === '') {
      //   return '取消'
      // }
      if (this.initStatus === 0) {
        return `+${this.item.guess_num || 0}`
      } else if (this.initStatus === 1) {
        return '进行中(比分)'
      }
      return '已结束(比分)'
    }
  },
  methods: {
    open () {
      const query = {
        smid: this.item.smid,
        gameId: this.item.game_type_id,
        matchStatus: this.MatchStatus * 1 + 1,
        patternId: this.Pattern.id
      }
      this.$router.push({ path: '/guessingDetails', query })
    },
    betting (index) {
      // index 0是主队1是客队
      if (!token.Token) {
        Toast('请登陆')
        this.$router.replace({
          path: '/login'
        })
        return
      }
      if (this.initStatus !== 0) {
        this.open()
        return
      }
      // 过关  // 早盘
      const item = { ...(this.item) }
      item.bet_now = index // 目标0是主队1是客队
      this.$store.commit('ADD_DEL_365_THROUGH_DATA', { item, patternId: this.Pattern.id })
    }
  }
}
</script>

<style scoped lang="scss">
  $border: 4px;
  .item {
    height: 36px;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
  }

  .item2 {
    border-top: 1px solid #494949;
  }

  .Bets-Item {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    margin-top: 5px;
    > div {
      width: 33.3%;
    }
    > div.top {
      > div:nth-child(1) {
        height: 26px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        background-color: #1C1C1C;
        border-top-left-radius: $border;
        padding-left: 5px;
        span {
          margin-left: 5px;
          flex: 1;
          color: #999;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
      > div:nth-child(3) {
        border-bottom-left-radius: $border;
        @extend .item2;
      }
      > div:nth-child(2), > div:nth-child(3) {
        @extend .item;
        background-color: #313131;
      }
    }
    > div.center {
      > p {
        height: 26px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-around;
        background-color: #1C1C1C;
        span {
          color: #999;
        }
      }
      > div:nth-child(3) {
        @extend .item2;
      }
      > div:nth-child(2), > div:nth-child(3) {
        @extend .item;
        background-color: #313131;
      }
    }
    > div.bottom {
      > div:nth-child(1) {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: center;
        height: 26px;
        background-color: #1C1C1C;
        border-top-right-radius: $border;
        color: #FFC63A;
        position: relative;
      }
      > div:nth-child(1).activation::after {
        content: '';
        display: inline-block;
        position: absolute;
        right: 0;
        width: 35px;
        height: 35px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("../../assets/competition/ic_guss@2x.png");
      }
      > div:nth-child(3) {
        @extend .item2;
        border-bottom-right-radius: $border;
      }
      > div:nth-child(2), > div:nth-child(3) {
        @extend .item;
        background-color: #313131;
        border-left: 1px solid #494949;
      }
    }
  }

  .state-0 {
    > div.bottom {
      > div:nth-child(2), > div:nth-child(3) {
        color: #fff;
        background: linear-gradient(180deg, rgba(63, 63, 63, 1) 0%, rgba(49, 49, 49, 1) 100%);
      }
      > div:nth-child(2):active, > div:nth-child(3):active {
        background: linear-gradient(0deg, rgba(153, 153, 153, 1) 0%, rgba(204, 204, 204, 1) 100%);
        color: #131313;
      }
      &.betting-0 > div:nth-child(2), &.betting-1 > div:nth-child(3) {
        background: linear-gradient(0deg, rgba(153, 153, 153, 1) 0%, rgba(204, 204, 204, 1) 100%);
        color: #131313;
      }
    }
  }
  .state-1 {
    > div.bottom {
      > div:nth-child(2), > div:nth-child(3) {
        color: #999;
      }
    }
  }

  .state-2, .state-3 {
    > div.bottom {
      > div:nth-child(1) {
        color: #999;
      }
      > div:nth-child(2), > div:nth-child(3) {
        color: #999;
      }
    }
  }
</style>
