<template>
  <div class="bet-item">
    <div>
      <span class="icon" v-bind:style="{ backgroundImage: 'url(' + item.icon + ')'}"></span>{{item.bet_match_name}}
      <span class="right">{{item.create_time | dateFormat('yyyy-MM-dd')}}</span>
    </div>
    <div>{{item.host_team_name}}&emsp;VS&emsp;{{item.guest_team_name}}</div>
    <div>
      预测{{round[item.round]}}{{item.bet_team_name}}{{item.bet_play_name}}
      <b class="color-red right">已下注：{{item.total_amount}}</b>
    </div>
    <div>
      预计收益：{{item.total_amount * item.bet_odds / 1000 | numFilter}}<span class="diamond">钻石</span><span class="cash">元</span>
      <b class="right" :class="'color-'+item.distribute_status+'-'+item.winnings_status+'-'+item.action_type">
        结果：
        <template v-if="item.distribute_status === 0">
          未结算
        </template>
        <template v-else>
          <template v-if="item.winnings_status === 0">
            未中奖
          </template>
          <template v-else-if="item.winnings_status === 1">
            <template v-if="item.action_type === 1">
              返还{{item.winnings_bonus}}<span class="diamond">钻石</span><span class="cash">元</span>
            </template>
            <template v-else>
              获得{{item.winnings_bonus}}<span class="diamond">钻石</span><span class="cash">元</span>
            </template>
          </template>
        </template>
      </b>
    </div>
  </div>
</template>
<script>
export default {
  name: 'betItem',
  props: {
    item: { type: Object }
  },
  data: function () {
    return {
      value: '',
      round: ['', '第一局', '第二局', '第三局', '第四局', '第五局']
    }
  }
}
</script>
<style scoped lang="scss">
  .bet-item {
    text-align: left;
    background-color: $background-light-color;
    color: $font-color;
    margin-bottom: 12px;
    font-size: 15px;
    > div{
      padding: 15px 15px;
    }
    > div:nth-child(1){
      padding: 0 15px;
      height: 32px;
      line-height: 32px;
      border-bottom: 1px solid $border-color;
      font-size: 13px;
      @if $IF365 { color: #CCC; }
      @else { color: #999; }
      > span.icon {
        display: inline-block;
        width: 23px; height: 23px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        float: left;
        margin-top: 3px;
        margin-right: 10px;
      }
    }
    > div:nth-child(2){
      padding: 15px 15px 10px 15px;
    }
    > div:nth-child(3){
      padding: 0 15px;
    }
    > div:nth-child(4){
      padding: 17px 15px 15px 15px;
    }
  }
  [class^="color-0-"] {
    @if $IF365 { color: #FFF9E0; }
    @else { color: #333; }
  }
  .color-1-0-0 {
    @if $IF365 { color: #FFF9E0; }
    @else { color: #999; }
  }
  .color-1-1-0,
  .color-1-1-1 {
    color: $back-color;
    @if $IF365 { color: #FFF9E0; }
    @else { color: #5A2EFF; }
  }
  .color-red {
    @if $IF365 { color: $back-color; }
    @else { color: #ED3232; }
  }
  .right {
    float: right;
  }
  .diamond {
    @if $IF365 { display: none; }
    @else { display: inline-block; }
  }
  .cash {
    @if $IF365 { display: inline-block; }
    @else { display: none; }
  }
</style>
