<template>
  <div class="web-page">
    <nav-head v-if="showHead" :title="title" fixed-or-transparency="isFixed NoTransparency"/>
    <iframe ref="content" :src="url" @load="OnLoad" @error="hide" scrolling="auto" frameborder="0"></iframe>
  </div>
</template>

<script>//
import loading from '../tool/loading'
import { token } from '../tool/storeManage'
import { Toast } from 'mint-ui'
import { GET_USER_INFO } from '../vuex/Mine'
import { mapActions } from 'vuex'

export default {
  name: 'webPage',
  data () {
    return {
      showHead: true,
      title: '加载中...',
      url: '',
      post: false // 是否监听message事件
    }
  },
  methods: {
    ...mapActions({
      getUserInfo: GET_USER_INFO
    }),
    receiveMessage (event) {
      // console.log('Parent', event)
      if (event.data && typeof event.data === 'string') {
        const data = JSON.parse(event.data)
        if (data.response >= 0) {
          const path = {}
          if (data.response === 2) {
            path.path = '/competition'
          } else if (!token.Token || data.response === 0) {
            this.goLogin()
            return
          } else if (data.response === 1) {
            path.path = '/addressList'
          } else if (data.response === 3) {
            path.path = '/recharge'
          } else if (data.response === 4) {
            this.$router.replace({ path: '/' })
            return
          } else if (data.response === 5) {
            if (token.Token) {
              this.getUserInfo()
            } else {
              this.goLogin()
            }
            return
          } else if (data.response === 6) {
            this.$router.back()
            return
          }
          path.path && this.$router.push(path)
        } else if (data.response === -1) {
          console.log('ok', event)
        }
      }
    },
    goLogin () {
      Toast('请登陆')
      this.$router.replace({
        path: '/login',
        query: { ...(this.$route.query), back: false, redirect: this.$route.path }
      })
    },
    OnLoad () {
      this.hide()
      this.$refs.content.style.height = (window.innerHeight - (this.showHead ? 40 : 0)) + 'px'
      this.post && this.$refs.content.contentWindow.postMessage(token.Token || '', `*`)
    },
    hide () {
      loading.hide()
    }
  },
  created () {
    loading.show()
    const query = this.$route.query
    query.title && (this.title = query.title)
    query.showHead === 'false' && (this.showHead = false)
    if (query.post) {
      this.post = true
      this.url = `${query.url}?ish5=true&token=${token.Token}`
      // this.url = `http://172.16.3.11:8080/#/?ish5=true&token=${token.Token}`
      window.addEventListener('message', this.receiveMessage, false)
    } else {
      this.url = query.url
    }
  },
  beforeDestroy () {
    this.post && window.removeEventListener('message', this.receiveMessage, false)
  }
}
</script>

<style>
  .web-page {
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 640px;
    -webkit-overflow-scrolling: touch;
    overflow-y: auto;
    overflow-x: hidden;
  }

  .web-page iframe {
    width: 100%;
    height: 100%;
    z-index: 0;
  }
</style>
