export function timeFmt (value, fmt) {
  var time = new Date(value * 1000)
  const o = {
    'M+': time.getMonth() + 1, // 月份
    'd+': time.getDate(), // 日
    'h+': time.getHours(), // 小时
    'm+': time.getMinutes(), // 分
    's+': time.getSeconds(), // 秒
    'q+': Math.floor((time.getMonth() + 3) / 3), // 季度
    'W': weekFormatting(time.getDay()), // 星期
    'S': time.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (time.getFullYear() + '').substr(4 - RegExp.$1.length))
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return fmt
}

export function copy (obj) {
  const type = Object.prototype.toString.call(obj)
  let val
  if (type === '[object Array]') {
    val = []
    for (let i in obj) val[i] = copy(obj[i])
  } else if (type === '[object Object]') {
    val = {}
    for (let i in obj) val[i] = copy(obj[i])
  } else {
    return obj
  }
  return val
}

export function bubbleSort (arr) {
  // 编码排序
  for (let i = 0; i < arr.length; i++) {
    for (let j = 0; j < arr.length - 1 - i; j++) {
      if (arr[j].charCodeAt(0) > arr[j + 1].charCodeAt(0)) {
        const q = arr[j]
        arr[j] = arr[j + 1]
        arr[j + 1] = q
      }
    }
  }
  return arr
}

export function initMatchList (id) {
  const list = [0, 998, 999]
  return list.some(i => i === id)
}

