export function toFixed (value) {
  if (typeof value === 'number') {
    return (value).toFixed(2)
  }
  return ''
}

// 兼容ios(value格式为2017-11-11 0:0:0)
export function formatTimeStamp (value) {
  const re = /^[1-9]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1])\s+(20|21|22|23|[0-1]\d):[0-5]\d:[0-5]\d$/
  const re2 = /^[1-2][0-9][0-9][0-9]-[0-1]{0,1}[0-9]-[0-3]{0,1}[0-9]$/
  if (re2.test(value) || re.test(value)) {
    if (Date.parse(new Date(value))) {
      value = Date.parse(new Date(value)) / 1000
    } else {
      value = Date.parse(new Date(value.replace(/-/g, '/'))) / 1000
    }
  }
  return value
}

