export function weekFormatting (value) {
  switch (value) {
  case 1:
    return '星期一'
  case 2:
    return '星期二'
  case 3:
    return '星期三'
  case 4:
    return '星期四'
  case 5:
    return '星期五'
  case 6:
    return '星期六'
  case 0:
    return '星期日'
  default:
    return value
  }
}

const iFPhone = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)

export default {
  isMobile (mobile) {
    return /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(17[0-9]{1}))+\d{8})$/.test(mobile)
  },
  passwordValid (value) {
    return /^(?=.*\d)[a-zA-Z\d]{6,18}$/.test(value)
  },
  smsValid (value) {
    return /^\d{6}$/.test(value)
  },
  isQQ (qq) {
    return /^([1-9][0-9]{4})|([0-9]{6,10})/.test(qq)
  },
  iFPhone
}
