import http from '../tool/http'
import { copy } from '../tool/util'

export const GET_HOME_DATA = 'GET_HOME_DATA'
export const GET_MSG_LIST_DATA = 'GET_MSG_LIST_DATA'
export const SET_GENRE_INDEX = 'SET_GENRE_INDEX'
export const SET_GENRE = 'SET_GENRE'
export const SET_BANNER = 'SET_BANNER'
export const SET_MATCH_RECOMMEND = 'SET_MATCH_RECOMMEND'
export const SET_MSG_LISTS = 'SET_MSG_LISTS'
export const ADD_SET_MSG_LISTS = 'ADD_SET_MSG_LISTS'

export const HomeMutations = {
  [SET_GENRE_INDEX] (state, params) {
    state.GenreIndex = params
  },
  [SET_GENRE] (state, params) {
    state.Genre = params
  },
  [SET_BANNER] (state, params) {
    const Banner = copy(state.Banner)
    Banner[state.GenreIndex] = params
    state.Banner = Banner
  },
  [SET_MATCH_RECOMMEND] (state, params) {
    const MatchRecommend = copy(state.MatchRecommend)
    MatchRecommend[state.GenreIndex] = params
    state.MatchRecommend = MatchRecommend
  },
  [SET_MSG_LISTS] (state, params) {
    const MsgList = copy(state.MsgList)
    MsgList[state.GenreIndex] = params
    state.MsgList = MsgList
  },
  [ADD_SET_MSG_LISTS] (state, params) {
    const MsgList = copy(state.MsgList)
    MsgList[state.GenreIndex].push(...params)
    state.MsgList = MsgList
  }
}

export const HomeActions = {
  [GET_MSG_LIST_DATA] ({ commit, dispatch, getters, rootGetters, rootState, state }) {
    return http.getHomePortalLists(state.Genre[state.GenreIndex].id, state.MsgList[state.GenreIndex].length / 10 + 1).then(list => {
      if (list && list.list.length) {
        commit(ADD_SET_MSG_LISTS, list.list)
        return Promise.resolve(list.list.length)
      }
    })
  },
  [GET_HOME_DATA] ({ commit, state }, { type, index }) {
    return new Promise((resolve) => {
      if (type === 0) {
        // 第一次
        index = state.GenreIndex
        f1(true)
      } else if (type === 1) {
        // 切换
        resolve({ id: state.Genre[index].id, inspect: true })
      } else if (type === 2) {
        // 更新
        index = state.GenreIndex
        f1()
      }

      function f1 (ins) {
        if (ins && state.Genre[index] && (state.Genre[index].id === 0 || state.Genre[index].id)) {
          resolve({ id: state.Genre[index].id, inspect: true })
        } else {
          http.getHomeMenu().then(data => {
            if (data[index] && data[index].game_name) {
              commit(SET_GENRE, data)
              resolve({ id: data[index].id })
            }
          })
        }
      }
    }).then(({ id, inspect }) => {
      const Arr = []
      if (inspect) {
        state.Banner[index] && state.Banner[index][0] && state.Banner[index][0].id ? Arr.push(null) : Arr.push(http.getHomeSlides(id))
        state.MatchRecommend[index] && state.MatchRecommend[index].length ? Arr.push(null) : Arr.push(http.getHomeMatchRecommend(id))
        state.MsgList[index] && state.MsgList[index].length ? Arr.push(null) : Arr.push(http.getHomePortalLists(id))
      } else {
        Arr.push(http.getHomeSlides(id), http.getHomeMatchRecommend(id), http.getHomePortalLists(id))
      }
      if (index !== state.GenreIndex) {
        commit(SET_GENRE_INDEX, index)
      }

      Arr[0] && Arr[0].then(list => {
        if (list && list.length) {
          const item = list.find(items => items.id === id)
          if (item !== -1 && item.items.length) {
            commit(SET_BANNER, item.items)
          }
        }
      })
      Arr[1] && Arr[1].then(list => {
        if (list && list.length) {
          commit(SET_MATCH_RECOMMEND, list)
        }
      })
      Arr[2] && Arr[2].then(list => {
        if (list && list.list && list.list.length) {
          commit(SET_MSG_LISTS, list.list)
        }
      })
    })
  }
}
