import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/public/Login.vue'
import WebPage from './views/WebPage.vue'
import loading from './tool/loading'
import { token } from './tool/storeManage'

Vue.use(Router)
const routerList = []
if (process.build.IF365) {
  // 365电竞
  routerList.push(
    {
      path: '/',
      component: require('./components/365/Index.vue').default,
      children: [
        {
          path: '',
          component: require('./views/365dj/competition/Competition365.vue').default
        },
        {
          path: '/score',
          name: 'Score',
          component: require('./views/365dj/score/score.vue').default
        },
        {
          path: 'store',
          component: require(/* webpackChunkName: "store365" */'./views/365dj/order/scheme365.vue').default,
          meta: { requiresAuth: true }
        },
        {
          path: 'mine',
          name: 'mine',
          component: require('./views/365dj/mine/Mine.vue').default,
          meta: { requiresAuth: true }
        }
      ]
    },
    {
      path: '/set',
      name: 'set',
      component: () => import('./views/365dj/mine/Set.vue')
    },
    {
      path: '/exchangeHistory',
      name: 'exchangeHistory',
      component: () => import('./views/365dj/mine/History.vue')
    },
    {
      path: '/recharge',
      name: 'recharge',
      component: () => import('./views/365dj/mine/RechargeChannel.vue')
    },
    {
      path: '/recharge365',
      name: 'recharge365',
      component: () => import('./views/365dj/mine/Recharge.vue')
    },
    {
      path: '/bindBank',
      name: 'bindBank',
      component: () => import('./views/365dj/mine/BindBank.vue')
    },
    {
      path: '/userBankInfo',
      name: 'userBankInfo',
      component: () => import('./views/365dj/mine/UserBankInfo.vue')
    },
    {
      path: '/withDraw',
      name: 'withDraw',
      component: () => import('./views/365dj/mine/WithDraw.vue')
    },
    {
      path: '/rechargeByAlipayQRCode',
      name: 'rechargeByAlipayQRCode',
      component: () => import('./views/365dj/mine/RechargeByAlipayQRCode.vue')
    },
    {
      path: '/rechargeByUnionPay',
      name: 'rechargeByUnionPay',
      component: () => import('./views/365dj/mine/RechargeByUnionPay.vue')
    },
    {
      path: '/moneyLog',
      name: 'moneyLog',
      component: () => import('./views/365dj/mine/MoneyLog.vue')
    },
    {
      path: '/guessForm', // 竞猜单
      name: 'guessForm',
      component: () => import(/* webpackChunkName: "GuessingDetails" */ './views/365dj/competition/children/GuessForm.vue')
    },
    {
      path: '/bettingDetails', // 投注详情
      name: 'bettingDetails',
      component: () => import(/* webpackChunkName: "GuessingDetails" */ './views/365dj/order/BettingDetails.vue')
    },
    {
      path: '/guessingDetails', // 比赛详情
      name: 'guessingDetails',
      component: () => import(/* webpackChunkName: "GuessingDetails" */ './views/365dj/competition/children/365GuessingDetails.vue')
    },
    {
      path: '/redEnvelopesList', // 比赛详情
      name: 'RedEnvelopesList',
      component: () => import(/* webpackChunkName: "GuessingDetails" */ './views/365dj/mine/RedEnvelopesList.vue')
    },
    {
      path: '/redPacketsSelected', // 使用红包
      name: 'RedPacketsSelected',
      component: () => import(/* webpackChunkName: "GuessingDetails" */ './views/365dj/mine/RedPacketsSelected.vue')
    },
    {
      path: '/myInvite', // 我的邀请
      name: 'myInvite',
      component: () => import(/* webpackChunkName: "GuessingDetails" */ './views/365dj/mine/MyInvite.vue')
    }
    // {
    //   path: '/guessingDetails', // 比赛详情
    //   name: 'guessingDetails',
    //   component: () => import(/* webpackChunkName: "GuessingDetails" */ './views/hmdj/competition/GuessingDetails.vue')
    // }
  )
} else {
  // 黑马
  routerList.push(
    {
      path: '/',
      component: require('./components/Index.vue').default,
      children: [
        {
          path: '',
          component: require('./views/hmdj/home/Home.vue').default,
          children: [
            {
              path: '',
              component: require('./views/hmdj/home/children/Information.vue').default
            }, {
              path: 'searchHistory',
              component: require('./views/hmdj/home/children/SearchHistory.vue').default
            }, {
              path: 'searchResult',
              component: require('./views/hmdj/home/children/SearchResult.vue').default
            }
          ]
        },
        {
          path: 'competition',
          component: () => import(/* webpackChunkName: "Competition" */ './views/hmdj/competition/Competition.vue')
        },
        {
          path: 'store',
          component: () => import(/* webpackChunkName: "store" */'./views/hmdj/store/Store.vue')
        },
        {
          path: 'mine',
          name: 'mine',
          component: () => import('./views/hmdj/mine/Mine.vue'),
          meta: { requiresAuth: true }
        }
      ]
    },
    {
      path: '/informationDetails', // 资讯详情
      component: () => import(/* webpackChunkName: "InformationDetails" */ './views/hmdj/home/children/InformationDetails.vue')
    },
    {
      path: '/set',
      name: 'set',
      component: () => import('./views/hmdj/mine/set/Set.vue')
    },
    {
      path: '/exchangeHistory',
      name: 'exchangeHistory',
      component: () => import('./views/hmdj/mine/ExchangeHistory.vue')
    },
    {
      path: '/recharge',
      name: 'recharge',
      component: () => import('./views/hmdj/mine/Recharge.vue')
    },
    {
      path: '/goodDetail',
      name: 'goodDetail',
      component: () => import('./views/hmdj/store/children/GoodDetail.vue'),
      meta: { requiresAuth: true }
    },
    {
      path: '/betHistory',
      name: 'betHistory',
      component: () => import(/* webpackChunkName: "BetHistory" */ './views/hmdj/mine/BetHistory.vue') // 我的订单
    },
    {
      path: '/guessingDetails', // 比赛详情
      name: 'guessingDetails',
      component: () => import(/* webpackChunkName: "GuessingDetails" */ './views/hmdj/competition/GuessingDetails.vue')
    }
  )
}

const router = new Router({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes:
    [
      ...routerList,
      {
        path: '/webPage', // iframe
        name: 'webPage',
        component: WebPage
      },
      {
        path: '/login', // 登录
        name: 'Login',
        component: Login
      },
      {
        path: '/register', // 注册
        name: 'register',
        component: () => import(/* webpackChunkName:"register" */'./views/public/Register.vue')
      },
      {
        path: '/forgetPassword',
        component: () => import(/* webpackChunkName:"forgetPassword" */'./views/public/ForgetPassword.vue')
      },
      {
        path: '/agreement',
        component: () => import(/* webpackChunkName:"agreement" */'./views/public/Agreement.vue')
      },
      {
        path: '/bindPhone',
        name: 'bindPhone',
        component: () => import(/* webpackChunkName:"bindPhone" */'./views/public/BindPhone.vue')
      },
      {
        path: '/accountBind',
        name: 'accountBind',
        component: () => import(/* webpackChunkName: "AccountBind" */ './views/public/set/children/AccountBind.vue')
      },
      {
        path: '/modifyBindPhone',
        name: 'modifyBindPhone',
        component: () => import(/* webpackChunkName: "ModifyBindPhone" */ './views/public/set/children/ModifyBindPhone.vue')
      },
      {
        path: '/resetPassword',
        name: 'resetPassword',
        component: () => import(/* webpackChunkName: "ResetPassword" */ './views/public/set/children/ResetPassword.vue')
      },
      {
        path: '/addressList',
        name: 'addressList',
        component: () => import(/* webpackChunkName: "AddressList" */ './views/public/set/children/AddressList.vue')
      },
      {
        path: '/addNewAddress',
        name: 'addNewAddress',
        component: () => import(/* webpackChunkName: "AddNewAddress" */ './views/public/set/children/AddNewAddress.vue')
      },
      {
        path: '/editAddress',
        name: 'editAddress',
        component: () => import(/* webpackChunkName: "EditAddress" */ './views/public/set/children/EditAddress.vue')
      },
      {
        path: '/aboutUs',
        name: 'aboutUs',
        component: () => import(/* webpackChunkName: "AboutUs" */ './views/public/set/children/AboutUs.vue')
      },
      {
        path: '/userInfo',
        name: 'userInfo',
        component: () => import(/* webpackChunkName: "UserInfo" */ './views/public/userInfo/UserInfo.vue')
      },
      {
        path: '/nickName',
        name: 'nickName',
        component: () => import(/* webpackChunkName: "EditNickName" */ './views/public/userInfo/children/EditNickName.vue')
      },
      {
        path: '/feedBack',
        name: 'feedBack',
        component: () => import(/* webpackChunkName: "FeedBack" */ './views/public/FeedBack.vue')
      },
      {
        path: '/help',
        name: 'help',
        component: () => import(/* webpackChunkName: "Help" */ './views/public/Help.vue')
      },
      {
        path: '/propLog',
        name: 'propLog',
        component: () => import(/* webpackChunkName: "PropLog" */ './views/public/recharge/children/PropLog.vue')
      },
      {
        path: '/diamondLog',
        name: 'diamondLog',
        component: () => import(/* webpackChunkName: "DiamondLog" */ './views/public/recharge/children/DiamondLog.vue')
      },
      {
        path: '/mineMessage',
        name: 'mineMessage',
        component: () => import(/* webpackChunkName: "mineMessage" */'./views/public/message/mineMessage.vue')
      },
      {
        path: '/messageList',
        name: 'messageList',
        component: () => import(/* webpackChunkName: "messageList" */'./views/public/message/messageList.vue')
      },
      {
        path: '/messageRevert',
        name: 'messageRevert',
        component: () => import(/* webpackChunkName: "messageRevert" */'./views/public/message/messageRevert.vue')
      },
      {
        path: '/messageDetails', // 比赛详情
        name: 'messageDetails',
        component: () => import(/* webpackChunkName: "GuessingDetails" */ './views/public/message/messageDetails.vue')
      }
    ],
  scrollBehavior (to, from, savedPosition) {
    return new Promise((resolve, reject) => {
      resolve({ x: 0, y: 0 })
    })
  }
})

router.beforeEach(function (to, from, next) {
  if (to.meta.requiresAuth && !token.Token) {
    next({
      path: '/login',
      query: { redirect: to.fullPath }
    })
  } else {
    next()
  }
})
router.afterEach(function (to, from) {
  loading.hide()
})

export default router
