<template>
  <div>
    <div style="height: 83px">
      <div class="fixed-top">
        <the-game-switch @Switch="selected" show-recommended="true"></the-game-switch>
        <div class="list-of-species" :class="'opt-for-'+Status">
          <div @click="selected({status: 1})">全部</div>
          <div @click="selected({status: 2})">竞猜中</div>
          <div @click="selected({status: 3})">已结算</div>
        </div>
      </div>
    </div>
    <mt-loadmore
      :top-method="loadTop"
      :bottom-all-loaded="true"
      :autoFill="false"
      ref="loadmore"
      style="min-height: calc(100vh - 129px);"
    >
      <div class="bet-list"
           v-infinite-scroll="loadMore"
           infinite-scroll-disabled="loading"
           infinite-scroll-distance="10"
           infinite-scroll-immediate-check="false"
      >
        <bet-item
          v-for="item in (BetHistory[GenreIndex]&&BetHistory[GenreIndex][Status-1])||[]"
          :item="item"
          :key="item.id"
        />
        <no-content
          v-show="loading===1&&(!BetHistory[GenreIndex]||!BetHistory[GenreIndex][Status-1]||BetHistory[GenreIndex][Status-1].length===0)"
          img="2"
          :text="['没有竞猜信息!','快去竞猜吧']"
        />
      </div>
      <div v-if="loading!==1" v-show="loading" style="color: #999;text-align: center;line-height: 35px;">
        加载中•••
      </div>
    </mt-loadmore>
  </div>
</template>
<script>//
import TheGameSwitch from '../components/TheGameSwitch'
import BetItem from '../components/BetItem.vue'
import { Button, Loadmore } from 'mint-ui'
import { mapActions, mapState } from 'vuex'
import { GET_BET_HISTORY, GET_BET_HISTORY_LIST } from '../vuex/Mine'
import NoContent from '../components/NoContent'

function initLoading () {
  if (this.BetHistory[this.GenreIndex] && this.BetHistory[this.GenreIndex][this.Status - 1] && this.BetHistory[this.GenreIndex][this.Status - 1].length) {
    const len = this.BetHistory[this.GenreIndex][this.Status - 1].length
    this.loading = (len % 10 === 0) ? false : 1
  } else {
    this.loading = 1
  }
}

export default {
  name: 'BetScheme',
  data: function () {
    return {
      loading: true
    }
  },
  created () {
    this.getCompetitionData({ type: 0 }).then(() => {
      initLoading.call(this)
    })
  },
  computed: {
    ...mapState({
      Status: state => state.mine.BetStatus,
      BetHistory: state => state.mine.BetHistory,
      GenreIndex: state => state.home.GenreIndex
    })
  },
  methods: {
    loadTop () {
      // 更新数据
      this.loading = true
      this.getCompetitionData({ type: 2 }).then(() => {
        initLoading.call(this)
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded()
        }, 500)
      })
    },
    loadMore () {
      // 加载更多数据
      this.loading = true
      const len = this.BetHistory[this.GenreIndex][this.Status - 1].length
      this.getBrtList().then(() => {
        const len2 = this.BetHistory[this.GenreIndex][this.Status - 1].length
        if (len !== len2 && len2 % 10 === 0) {
          // 长度变化且能被整除
          this.loading = false
        } else {
          // 长度没变拿不到新数据了,永远禁止上拉加载
          this.loading = 1
        }
      })
    },
    selected ({ index, status }) {
      this.loading = true
      this.getCompetitionData({ index, status, type: 1 }).then(() => {
        initLoading.call(this)
      })
    },
    ...mapActions({
      getCompetitionData: GET_BET_HISTORY,
      getBrtList: GET_BET_HISTORY_LIST
    })
  },
  components: {
    [Button.name]: Button,
    [Loadmore.name]: Loadmore,
    BetItem,
    TheGameSwitch,
    NoContent
  }
}
</script>
<style scoped lang="scss">
  div.fixed-top {
    position: fixed;
    top: 46px;
    width: 100%;
    max-width: 640px;
    z-index: 1;
  }

  div.list-of-species {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    padding: 7px 0;
    @if $IF365 {
      background-color: $background-color;
    } @else {
      background-color: white;
    }
    div {
      line-height: 1;
      margin-left: 15px;
      font-size: 14px;
      padding: 6px 11px;
      color: rgba(153, 153, 153, 1);
      background: rgba(226, 226, 226, 1);
      border-radius: 0 13px 13px 13px;
    }
    &.opt-for-3 div:nth-of-type(3), &.opt-for-2 div:nth-of-type(2), &.opt-for-1 div:nth-of-type(1) {
      background-color: $back-color;
      color: white;
    }
  }
</style>
