<template>
  <router-link tag="li" :to="{
        path: '/guessingDetails',
        query: {
          smid: item.smid,
          gameId: item.game_type_id,
          matchStatus: matchStatus,
          patternId: Pattern.id
        }
      }" class="competition-score">
    <div>
      <p><img :src="item.icon" alt=""><span>{{item.match_type}}</span></p>
      <p>{{item.game_name}}</p>
      <p><span v-show="matchStatus===1">{{ item.round |InitScore}}</span></p>
    </div>
    <div>
      <div>
        <div><img :src="item.host_team_logo" alt=""></div>
        <div :class="{'end-status':matchStatus===2}">
          <b>{{ item.sports_match_score|score(true) }}</b>
          <span>︰</span>
          <b>{{ item.sports_match_score|score(false) }}</b>
        </div>
        <div><img :src="item.guest_team_logo" alt=""></div>
      </div>
      <div>
        <b>{{item.host_team_name}}</b>
        <p>{{item.game_start_time|dateFormat('hh:mm')}}</p>
        <b>{{item.guest_team_name}}</b>
      </div>
    </div>
  </router-link>
</template>

<script>//
import { InitScore, score } from '../../tool/filter'
import { mapState } from 'vuex'

export default {
  name: 'ScoreItem',
  props: {
    item: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  computed: {
    ...mapState({
      Pattern: state => state.home.Pattern[state.home.PatternIndex] || {},
      matchStatus: state => {
        if (state.home.MatchStatus > 1) {
          // 进行中已结束
          return state.home.MatchStatus * 1 - 1
        } else {
          // 赛程赛果
          return state.home.MatchStatus * 1 + 1
        }
      }
    })
  },
  filters: {
    score,
    InitScore
  }
}
</script>

