<template>
  <div style="background-color: #131313;overflow: hidden;">
    <game-list :list="firstLine" @Switch="SwitchIndex1" :icon="score"/>
    <game-list v-show="!score" :list="SecondElements" @Switch="SwitchIndex1"/>
    <div class="competition-state" :class="'activation-'+MatchStatus">
      <div @click="SwitchIndex2(0)" v-show="!score">赛程</div>
      <div @click="SwitchIndex2(2)" v-show="score">进行中</div>
      <span style="width: 1px;height: 20px;background-color: #313131"></span>
      <div @click="SwitchIndex2(1)" v-show="!score">赛果</div>
      <div @click="SwitchIndex2(3)" v-show="score">已结束</div>
    </div>
  </div>
</template>

<script>//
import { mapActions, mapState } from 'vuex'
import { GET_365_HOME_DATA } from '../../vuex/365/home'
import { token } from '../../tool/storeManage'
import { Toast } from 'mint-ui'
import GameList from './GameList'

export default {
  components: { GameList },
  name: 'GameSwitching',
  props: {
    score: { type: Boolean }
  },
  computed: {
    ...mapState({
      clientWidth: state => state.mine.Device.width,
      PatternIndex: state => state.home.PatternIndex,
      GenreIndex: state => state.home.GenreIndex,
      MatchStatus: state => state.home.MatchStatus,
      firstLine: state => {
        if (!state.home.Genre.length) {
          return [{ game_name: '加载中...', icon: require('../../assets/home/ic_placepic@2x.png'), index: -1, id: -1 }]
        }
        const Genre = []
        state.home.Genre.forEach((n, i) => {
          if (!(n.id === 999 || n.id === 998)) {
            Genre.push({ index: i, ...n })
          }
        })
        return Genre
      },
      SecondElements: state => {
        if (!state.home.Genre.length) {
          return [{ game_name: '加载中...', icon: require('../../assets/home/ic_placepic@2x.png'), index: -1, id: -1 }]
        }
        const Genre = []
        state.home.Genre.forEach((n, i) => {
          if (n.id === 999 || n.id === 998) {
            Genre.push({ index: i, ...n })
          }
        })
        return Genre
      }
    })
  },
  methods: {
    ...mapActions({
      getHomeData: GET_365_HOME_DATA
    }),
    SwitchIndex1 (index, item) {
      if (index === this.GenreIndex) return

      if (item.id === 998 || item.id === 999) {
        if (token.Token) {
          this.$router.push({
            path: '/webPage',
            // query: { url: `http://172.16.3.30:8080/#/betting/football`, title: '365彩票', showHead: 'false', post: true }
            query: { url: item.url, title: '365彩票', showHead: 'false', post: true }
          })
        } else {
          Toast('请登陆')
          this.$router.replace({ path: '/login' })
        }
      } else {
        let index3 = 0
        if (this.MatchStatus > 1) {
          index3 = 2
        }
        this.getHomeData({ type: 1, index1: this.PatternIndex, index2: index, index3 }).then(() => {
          this.$emit('change')
        })
      }
    },
    SwitchIndex2 (index) {
      if (index === this.MatchStatus) return
      this.getHomeData({ type: 1, index1: this.PatternIndex, index2: this.GenreIndex, index3: index })
      this.$emit('change')
    }
  }
}
</script>

