import { Indicator } from 'mint-ui'

export default {
  state: false,
  show () {
    if (!this.state) {
      Indicator.open({
        spinnerType: 'fading-circle'
      })
      this.state = true
    }
  },
  hide () {
    if (this.state) {
      Indicator.close()
      this.state = false
    }
  }
}
