<template>
  <div>
    <div class="competition365-title">
        <nav-switch  @change="change"/>
        <game-switching @change="change"/>
      </div>
    <Loadmore :top-method="loadTop" ref="loadmore" spaceBetween="210">
      <ul class="Competition365-body" :style="{minHeight: `${Device.height-210-49}px`}">
        <no-content
                :text="['没有比赛信息!','去其它地方看看吧']"
                img="2"
                tag="li"
                v-show="loading && !MatchSchedule.length"
        />
        <li v-for="(n,i) in MatchSchedule" :key="i">
          <div class="item-title-date">
            <div>
              <div><span>{{ n.date|dateFormatDay('MM-dd',3651) }}</span><span> {{n.schedule_list.length}}场比赛</span>
              </div>
            </div>
          </div>
          <ul>
            <li v-for="(nn,ii) in n.schedule_list" :key="i+''+ii" style="margin: 0 13px">
              <bets-item
                      :aims="[i,ii]"
                      :item="nn"
                      v-if="nn.category===0||MatchStatus"
                      v-show="nn.odds.length"
              />
              <lottery-bets-item v-else-if="nn.category===1" :item="nn"/>
              <lottery-bets-item2 v-else-if="nn.category===2" :item="nn"/>
            </li>
          </ul>
        </li>
      </ul>
    </Loadmore>
    <router-link :to="{path:'/guessForm',query:{patternId:Pattern.id}}" class="thumbtack" tag="div"
                 v-show="PassStatus.show">
      <div><span>{{ PassStatus.betLength }}</span></div>
      <p><span>竞猜单</span></p>
      <p>总<br>赔<br>率<br></p>
      <p v-show="Pattern.id!==1">{{ PassStatus.odds|toFixed }}</p>
    </router-link>
  </div>
</template>

<script>//
import { Popup } from 'mint-ui'
import NavSwitch from '../../../components/365/NavSwitch'
import GameSwitching from '../../../components/365/GameSwitching'
import BetsItem from '../../../components/365/BetsItem'
import { mapActions, mapState } from 'vuex'
import { GET_365_HOME_DATA } from '../../../vuex/365/home'
import { GET_USER_INFO } from '../../../vuex/Mine'
import { toFixed } from '../../../tool/util'
import Loadmore from '../../../components/loadmore'

export default {
  name: 'Competition365',
  created () {
    const data = { type: 0 }
    if (this.MatchStatus > 1) data.index3 = 0
    this.getHomeData(data).finally(() => {
      this.loading = true
    })
  },
  mounted () {
    this.$nextTick(() => {
      let time = null
      window.onscroll = () => {
        // scrollTop就是触发滚轮事件时滚轮的高度
        clearTimeout(time)
        time = setTimeout(() => {
          !this.nodeList.length && (this.nodeList = document.querySelectorAll('.item-title-date'))
          const scrollTop = (document.documentElement.scrollTop || document.body.scrollTop) + 210
          let fixed = true
          for (let i = this.nodeList.length - 1; i >= 0; i--) {
            if (fixed && scrollTop > this.nodeList[i].offsetTop) {
              fixed = false
              this.nodeList[i].children[0].style = `position: fixed;z-index: 10;top: 210px;left:50%;transform: translateX(-50%);padding: 0 13px;`
            } else {
              this.nodeList[i].children[0].style = ''
            }
          }
        }, 150)
      }
    })
  },
  data () {
    return {
      loading: false,
      nodeList: []
    }
  },
  computed: {
    ...mapState({
      Device: state => state.mine.Device,
      Pattern: state => state.home.Pattern[state.home.PatternIndex] || {},
      PassStatus: state => state.home.PassStatus,
      MatchStatus: state => state.home.MatchStatus,
      MatchSchedule: state => {
        if (state.home.MatchStatus < 2 && state.home.MatchSchedule[state.home.PatternIndex] &&
          state.home.MatchSchedule[state.home.PatternIndex][state.home.GenreIndex] &&
          state.home.MatchSchedule[state.home.PatternIndex][state.home.GenreIndex][state.home.MatchStatus]
        ) {
          return state.home.MatchSchedule[state.home.PatternIndex][state.home.GenreIndex][state.home.MatchStatus]
        }
        return []
      }
    })
  },
  methods: {
    ...mapActions({
      getHomeData: GET_365_HOME_DATA,
      getUserInfo: GET_USER_INFO
    }),
    change () {
      this.nodeList = []
    },
    loadTop () {
      this.getHomeData({ type: 2 }).then(() => {
        setTimeout(() => {
          this.$refs.loadmore && this.$refs.loadmore.onTopLoaded()
        }, 500)
      })
    }
  },
  filters: { toFixed },
  components: {
    Loadmore,
    LotteryBetsItem2: () => import('../../../components/365/LotteryBetsItem2'),
    LotteryBetsItem: () => import('../../../components/365/LotteryBetsItem'),
    BetsItem,
    GameSwitching,
    NavSwitch,
    [Popup.name]: Popup
  }
}
</script>

<style scoped lang="scss">
  div.thumbtack {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 65%;
    > div:nth-of-type(1) {
      border-radius: 50% 50% 0 0;
      overflow: hidden;
      background-color: white;
      text-align: center;
      > span {
        display: inline-block;
        width: 22px;
        height: 22px;
        line-height: 22px;
        margin: 7px auto;
        border-radius: 50%;
        color: white;
        background-color: #FF3333;
      }
    }
    > p:nth-of-type(2), > p:nth-of-type(1) {
      background-color: white;
      color: #131313;
      text-align: center;
      font-weight: bold;
    }

    > p:nth-of-type(2) {
      padding-bottom: 4px;
    }
    > p:nth-of-type(3) {
      padding-bottom: 4px;
      max-width: 3em;
      background-color: white;
      color: #FFC63A;
      text-align: center;
      border-radius: 0 0 4px 4px;
      word-wrap: break-word;
    }
  }

  .competition365-title {
      position: fixed;
      width: 100%;
      max-width: 640px;
      top: 0;
      margin: 0 auto;
      z-index: 2;
  }

  .Competition365-body {
    > li > div.item-title-date {
      height: 33px;
      overflow: hidden;
      width: 100%;
      padding: 0 13px;

      > div {
        width: 100%;
        max-width: 640px;
        padding-top: 5px;

        > div {
          width: 100%;
          text-align: center;
          font-size: 12px;
          line-height: 28px;
          color: #ccc;
          border-radius: 4px;
          background-color: #313131;

          span:first-child {
            margin-right: 1em;
          }
        }
        }
      }
  }

  .own-Model {
    position: fixed;
    background: #fff;
    left: 50%;
    right: auto;
    bottom: 0;
    max-width: 640px;
    width: 100%;
    z-index: 100;
    transform: translate3d(-50%, 0, 0);
  }

  .own-modal {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background: #000;
    z-index: 99;
  }
</style>
