<template>
  <div class="login">
    <nav-head title="登录" FixedOrTransparency="isFixed NoTransparency" :fn="goBack"/>
    <div class="padding-0-25">
      <div class="logo"></div>
      <input class="phone" type="text" @input="handleInputNum" maxlength="11" v-model="phone" placeholder="请输入手机号">
      <input class="pwd" type="password" maxlength="18" v-model="password" placeholder="请输入密码"
             @keyup.enter="submit">
      <mt-button size="large" v-bind:disabled="!phone || !password"   @click.native="submit" class="btn-login" v-bind:class="{'available': phone && password }">立即登录</mt-button>
      <div class="margin-top-20 text-right" v-bind:style="{ marginBottom: clientHeight + 'px' }">
        <router-link to="forgetPassword" tag="a" class="btn-forget">忘记密码</router-link>
      </div>
      <div class="bottom">
        <router-link to="register" class="register-item">
          <img src="../../assets/login/ic_phone@2x.png"/>
          <p>手机注册</p>
        </router-link>
        <!--<div @click="toWeiXin" class="register-item" style="margin: 0 16%;">-->
          <!--<img src="../../assets/login/ic_wechat@2x.png"/>-->
          <!--<p>微信登录</p>-->
        <!--</div>-->
        <!--<router-link to="bindPhone" class="register-item">-->
          <!--<img src="../../assets/login/ic_qq@2x.png"/>-->
          <!--<p>QQ登录</p>-->
        <!--</router-link>-->
      </div>
    </div>
  </div>
</template>
<script>//
import Util from '@/tool/util'
import { Button, Toast } from 'mint-ui'
import { mapActions } from 'vuex'
import { LOGIN } from '../../vuex/Mine'

export default {
  name: 'login',
  data: function () {
    return {
      password: '',
      phone: '',
      clientHeight: ''
    }
  },
  methods: {
    goBack () {
      const query = this.$route.query
      if (query.redirect && query.back /* === '/guessingDetails' */) {
        const redirect = query.redirect
        delete query.redirect
        this.$router.replace({ path: redirect, query })
      } else {
        this.$router.replace({ path: '/' })
      }
    },
    toWeiXin () {
      window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize#wechat_redirect'
    },
    handleInputNum (e) {
      this.phone = e.target.value.replace(/[^\d]/g, '')
    },
    submit () {
      if (this.phone && this.password) {
        if (!Util.isMobile(this.phone)) {
          Toast('请输入正确的手机号!')
          return
        }
        if (!this.password || this.password.length < 6) {
          Toast('请输入6-18位密码！')
          return
        }
        this.login({
          phone: this.phone,
          psw: this.password,
          deviceType: 'web'
        })
      }
    },
    ...mapActions({
      login: LOGIN
    })
  },
  components: {
    [Button.name]: Button
  },
  mounted () {
    this.clientHeight = (`${document.documentElement.clientHeight}` - 519) < 0 ? 10 : (`${document.documentElement.clientHeight}` - 519)
  }
}
</script>
<style scoped lang="scss">
  div.login {
    text-align: center;
    background-color: $background-light-color;
    min-height: 100%;
    div.logo {
      width: 153px;
      height: 153px;
      background-repeat: no-repeat;
      background-position: center center;
      margin: 0 auto;
      @if $IF365 { background-image: url('../../assets/login/ic_365@2x.png');background-size: 50% 50%; }
      @else { background-image: url('../../assets/login/ic_heima@2x.png');background-size: 100% 100%; }
    }
    input {
      border: none;
      outline: none;
      width: 100%;
      font-size: 16px;
      height: 56px;
      line-height: 56px;
      margin-bottom: 10px;
      color: $font-color;
      background: none;
      @if $IF365 { border-bottom: 1px solid $border-color; }
      @else { border-bottom: 1px solid #DDD; }
      border-radius: 0;
    }
    input.phone {
      @if $IF365 { background-image: url("../../assets/login/ic_count_365@2x.png"); }
      @else { background-image: url("../../assets/login/ic_count@2x.png"); }
      background-repeat: no-repeat;
      background-position: left center;
      background-size: auto 45%;
      padding-left: 9%;
    }
    input.pwd {
      @if $IF365 { background-image: url("../../assets/login/ic_num_365@2x.png"); }
      @else { background-image: url("../../assets/login/ic_num@2x.png"); }
      background-repeat: no-repeat;
      background-position: left center;
      background-size: auto 45%;
      padding-left: 9%;
    }
    .btn-login {
      border: none;
      background-color: #ccc;
      color: white;
      box-shadow: none;
      height: 48px;
      font-size: 17px;
      margin-top: 20px;
    }
    .btn-login.available{
      background-color: $back-color;
      @if $IF365 { color: #131313; }
    }
    .btn-forget{
      font-size: 15px;
      color: $back-color;
      text-decoration: none;
    }
    .register-item {
      display: inline-block;
      @if $IF365 { color: #ccc; }
      @else { color: #999; }
      font-size: 14px;
      text-decoration: none;
      img{
        width: 40px;
        height: 40px;
      }
    }
    div.bottom{
      width: 100%;
      padding: 0 0 15px 0;
    }
  }
  .padding-0-25 {
    padding: 0 25px;
  }
  .margin-top-20 {
    margin-top: 20px;
  }
  .text-right {
    text-align: right;
  }
</style>
