import Vue from 'vue'
import http from '../tool/http'
import { token } from '../tool/storeManage'
import router from '../router'
import { copy, initMatchList } from '../tool/util'
import { SET_GENRE, SET_GENRE_INDEX } from './Home'
import { Toast } from 'mint-ui'
import { SET_365_GENRE_DATA, SET_365_GENRE_INDEX } from './365/home'

export const LOGIN = 'LOGIN'
export const LOGOUT = 'LOGOUT'
export const SET_LOGOUT = 'SET_LOGOUT'
export const REGISTER = 'REGISTER'
export const SET_USER_TOKEN = 'SET_USER_TOKEN'
export const GET_USER_INFO = 'GET_USER_INFO'
export const SET_USER_INFO = 'SET_USER_INFO'
export const GET_BIND_USER_INFO = 'GET_BIND_USER_INFO'
export const SET_BIND_USER_INFO = 'SET_BIND_USER_INFO'
export const GET_ADDRESS_LIST = 'GET_ADDRESS_LIST'
export const SET_ADDRESS_LIST = 'SET_ADDRESS_LIST'
export const DELETE_ADDRESS = 'DELETE_ADDRESS'
export const ADD_ADDRESS = 'ADD_ADDRESS'
export const EDIT_ADDRESS = 'EDIT_ADDRESS'
export const EDIT_NICKNAME = 'EDIT_NICKNAME'
export const SET_NICKNAME = 'SET_NICKNAME'
export const GET_USER_CODE = 'GET_USER_CODE'
export const RESET_PASSWORD = 'RESET_PASSWORD'
export const FORGET_PASSWORD = 'FORGET_PASSWORD'
export const CHANGE_MOBILE = 'CHANGE_MOBILE'
export const SET_MOBILE = 'SET_MOBILE'
export const BIND_MOBILE = 'BIND_MOBILE'
export const GET_PROP_LIST = 'GET_PROP_LIST'
export const SET_PROP_LIST = 'SET_PROP_LIST'
export const GET_DIAMOND_LOG = 'GET_DIAMOND_LOG'
export const SET_DIAMOND_LOG = 'SET_DIAMOND_LOG'
export const GET_PROP_LOG = 'GET_PROP_LOG'
export const SET_PROP_LOG = 'SET_PROP_LOG'
export const GET_BET_HISTORY = 'GET_BET_HISTORY'
export const SET_BET_HISTORY = 'SET_BET_HISTORY'
export const SET_BET_STATUS = 'SET_BET_STATUS'
export const GET_RECHARGE_CHANNEL = 'GET_RECHARGE_CHANNEL'
export const SET_RECHARGE_CHANNEL = 'SET_RECHARGE_CHANNEL'
export const WECHAT_LOGIN = 'WECHAT_LOGIN'
export const BIND_WECHAT = 'BIND_WECHAT'
export const UNBIND_WECHAT = 'UNBIND_WECHAT'
export const SET_WECHAT_BIND_STATUS = 'SET_WECHAT_BIND_STATUS'
export const SET_AVATAR_IMG = 'SET_AVATAR_IMG'
export const CHECK_USER_BANK = 'CHECK_USER_BANK'
export const ADD_USER_BANK = 'ADD_USER_BANK'
export const SET_USER_BANK = 'SET_USER_BANK'
export const WITH_DRAW = 'WITH_DRAW'
export const GET_HISTORY = 'GET_HISTORY'
export const SET_HISTORY = 'SET_HISTORY'
export const GET_BET_HISTORY_LIST = 'GET_BET_HISTORY_LIST'
export const ADD_PAYMENT_MSG = 'ADD_PAYMENT_MSG'
