export const GET_INVITE = 'GET_INVITE'
export const SET_INVITE_DATA = 'SET_INVITE_DATA'
export const DELETE_COUPON = 'DELETE_COUPON'
export const SET_INVITED_COUPON = 'SET_INVITED_COUPON'
export const CHANGE_INVITED_STATUS = 'CHANGE_INVITED_STATUS'
export const GET_COUPON_LIST = 'GET_COUPON_LIST'
export const SET_COUPON_LIST = 'SET_COUPON_LIST'

export const MineMutations = {
  [DEVICE_RESIZE] (state, params) {
    state.Device = { width: params.width, height: params.height, navInfo: params.navInfo }
  },
  [SET_USER_INFO] (state, params) {
    state.User = params
  },
  [SET_USER_TOKEN] (state, params) {
    token.Token = params.token
    const redirect = router.currentRoute.query.redirect || '/mine'
    let query = router.currentRoute.query
    delete query.redirect
    router.replace({ path: redirect, query: query })
  },
  [SET_BIND_USER_INFO] (state, params) {
    state.BingUserInfo = params.userInfo
  },
  [SET_ADDRESS_LIST] (state, params) {
    state.AddressList = params
  },
  [SET_PROP_LIST] (state, params) {
    state.PropList = params
  },
  [SET_RECHARGE_CHANNEL] (state, params) {
    state.RechargeChannels = params
  },
  [SET_MOBILE] (state, params) {
    console.log(params)
    state.User.mobile = params
    router.go(-1)
  },
  [SET_DIAMOND_LOG] (state, params) {
    if (params.index === 1) {
      state.DiamondLog = params.data
    } else if (params.index === 2) {
      const List = copy(state.DiamondLog)
      List.push(...params.data)
      state.DiamondLog = List
    }
  },
  [SET_PROP_LOG] (state, params) {
    if (params.index === 1) {
      state.PropLog = params.data
    } else if (params.index === 2) {
      const List = copy(state.PropLog)
      List.push(...params.data)
      state.PropLog = List
    }
  },
  [SET_BET_STATUS] (state, params) {
    state.BetStatus = params
  },
  [SET_BET_HISTORY] (state, { index, data, status, loadStatus }) {
    const List = copy(state.BetHistory[index]) || []
    if (loadStatus === 2 && List && List[status - 1] && List[status - 1].length) {
      List[status - 1].push(...data)
    } else {
      List[status - 1] = data
    }
    Vue.set(state.BetHistory, index, List)
  },
  [SET_LOGOUT] (state) {
    token.Token = ''
    token.clearToken()
    router.replace({ name: 'Login' })
    state.User = {}
    state.BingUserInfo = {}
    state.BetStatus = 1
    state.BetHistory = []
    state.AddressList = []
    state.PropList = []
    state.DiamondLog = []
    state.PropLog = []
  },
  [SET_NICKNAME] (state, params) {
    state.User.user_nickname = params.user_nickname
    Toast({
      message: '修改昵称成功！',
      duration: 2000
    })
    router.go(-1)
  },
  [SET_WECHAT_BIND_STATUS] (state, params) {
    state.BingUserInfo.wxstatus = params
  },
  [SET_AVATAR_IMG] (state, params) {
    state.User.avatar = params
    Toast({
      message: '修改头像成功！',
      duration: 2000
    })
  },
  [SET_USER_BANK] (state, params) {
    state.UserBankInfo = params
  },
  [SET_HISTORY] (state, params) {
    if (params.index === 1) {
      state.History[params.type] = params.data
    } else if (params.index === 2) {
      const List = copy(state.History[params.type])
      List.push(...params.data)
      state.History[params.type] = List
    }
  },
  [SET_MONET_LOG] (state, params) {
    if (params.index === 1) {
      state.MoneyLog = params.data
    } else if (params.index === 2) {
      const List = copy(state.MoneyLog)
      List.push(...params.data)
      state.MoneyLog = List
    }
  },
  [SET_COUPON_LIST] (state, params) {
    state.Coupon = params
  },
  [SET_INVITE_DATA] (state, params) {
    state.InviteData = params
  },
  [SET_INVITED_COUPON] (state, params) {
    state.InvitedCoupon = params
  }
}

export const MineActions = {
  [LOGIN] ({ commit }, params = {}) {
    http.postLogin(params).then(data => {
      if (data) {
        commit(SET_USER_TOKEN, data)
      }
    })
  },
  [LOGOUT] ({ commit }, params = {}) {
    http.postLogout().then(data => {
      commit(SET_LOGOUT, data)
    })
  },
  [REGISTER] ({ commit }, params = {}) {
    http.postRegister(params).then(data => {
      Toast('注册并激活成功,请登录！')
      if (params.invite_code && parseFloat(data.coupon) > 0) {
        commit(SET_INVITED_COUPON, data.coupon)
      }
      router.replace({ name: 'Login' })
    })
  },
  [GET_USER_INFO] ({ commit, state }, params = {}) {
    return http.getUserInfo().then(data => {
      if (data) {
        commit(SET_USER_INFO, data)
      }
    })
  },
  [GET_BIND_USER_INFO] ({ commit }, params = {}) {
    http.getBingUserInfo().then(data => {
      if (data) {
        commit(SET_BIND_USER_INFO, data)
      }
    })
  },
  [GET_ADDRESS_LIST] ({ commit }, params = {}) {
    http.getAddressList().then(data => {
      if (data) {
        commit(SET_ADDRESS_LIST, data)
      }
    })
  },
  [GET_PROP_LIST] ({ commit }, params = {}) {
    http.getPropList().then(data => {
      if (data) {
        commit(SET_PROP_LIST, data)
      }
    })
  },
  [GET_RECHARGE_CHANNEL] ({ commit }, params = {}) {
    http.getRechargeChannel().then(data => {
      if (data) {
        commit(SET_RECHARGE_CHANNEL, data)
      }
    })
  },
  [DELETE_ADDRESS] ({ commit }, params = {}) {
    http.postDeleteAddress(params).then(data => {
      if (data) {
        commit(SET_PROP_LIST, data)
      }
    })
  },
  [ADD_ADDRESS] ({ commit }, params = {}) {
    http.postAddAddress(params).then(data => {
      router.go(-1)
    })
  },
  [EDIT_ADDRESS] ({ commit }, params = {}) {
    http.postEditAddress(params).then(data => {
      router.go(-1)
    })
  },
  [RESET_PASSWORD] ({ commit }, params = {}) {
    http.postResetPassword(params).then(data => {
      Toast({
        message: '重置密码成功！',
        duration: 2000
      })
      router.go(-1)
    })
  },
  [EDIT_NICKNAME] ({ commit }, params = {}) {
    http.postEditNickname(params).then(data => {
      commit(SET_NICKNAME, params)
    })
  },
  [GET_USER_CODE] ({ commit }, params = {}) {
    http.getUserCode(params).then(data => {
      console.log(data)
    })
  },
  [FORGET_PASSWORD] ({ commit }, params = {}) {
    http.postForgetPassword(params).then(data => {
      router.go(-1)
    })
  },
  [CHANGE_MOBILE] ({ commit }, params = {}) {
    http.postChangeMobile(params).then(data => {
      commit(SET_MOBILE, params.mobile)
    })
  },
  [BIND_MOBILE] ({ commit }, params = {}) {
    http.postBindMobile(params).then(data => {
      commit(SET_MOBILE, params.mobile)
    })
  },
  [GET_DIAMOND_LOG] ({ commit, state }, params = {}) {
    let len = 0
    if (params.index === 1) {
      len = 1
      state.DiamondLog = {}
    } else {
      len = state.DiamondLog.length / 10 + 1
    }
    http.getDiamondLog(len).then(data => {
      if (data && data.length) {
        let obj = { data: data, index: params.index }
        commit(SET_DIAMOND_LOG, obj)
      }
      params.func()
    })
  },
  [GET_PROP_LOG] ({ commit, state }, params = {}) {
    let len = 0
    if (params.index === 1) {
      len = 1
      state.PropLog = {}
    } else {
      len = state.PropLog.length / 10 + 1
    }
    http.getPropLog(len).then(data => {
      if (data && data.length) {
        let obj = { data: data, index: params.index }
        commit(SET_PROP_LOG, obj)
      }
      params.func()
    })
  },
  [GET_BET_HISTORY_LIST] ({ commit, state, rootState }) {
    // state.BetStatus 1 2 3
    const type = state.BetStatus < 2 ? '' : state.BetStatus - 2
    return http.getBetHistory(
      rootState.home.Genre[rootState.home.GenreIndex].id,
      type,
      state.BetHistory[rootState.home.GenreIndex][state.BetStatus - 1].length / 10 + 1
    ).then(list => {
      if (list && list.length) {
        commit(SET_BET_HISTORY, {
          data: list,
          index: rootState.home.GenreIndex,
          status: state.BetStatus,
          loadStatus: 2
        })
      }
    })
  },
  [GET_BET_HISTORY] ({ commit, dispatch, getters, rootGetters, rootState, state }, { type, index, status }) {
    return new Promise((resolve) => {
      let inspect1 = true// 检查类别数据是否存在
      let inspect2 = true// 检查列表数据是否存在
      if (type === 0) {
        // 正常加载
        index = rootState.home.GenreIndex
        status = state.BetStatus
      } else if (type === 1) {
        // 切换
        index === undefined && (index = rootState.home.GenreIndex)
        status === undefined && (status = 1)
      } else if (type === 2) {
        // 更新
        index = rootState.home.GenreIndex
        status = state.BetStatus
        inspect2 = false
      }

      if (inspect1 && rootState.home.Genre[index] && (rootState.home.Genre[index].id === 0 || rootState.home.Genre[index].id)) {
        if (initMatchList(rootState.home.Genre[index].id)) {
          initIndex(rootState.home.Genre[index].id)
        }
        if (inspect2 && state.BetHistory[index] && state.BetHistory[index][status - 1] && state.BetHistory[index][status - 1].length) {
          resolve()// '有数据,不需要获取新数据'
          return
        }
        Match(rootState.home.Genre[index].id)
      } else {
        http.getHomeMenu().then(data => {
          if (data[0] && data[0].id >= 0) {
            if (process.build.IF365) {
              commit(SET_365_GENRE_DATA, data)
            } else {
              commit(SET_GENRE, data)
            }
            if (initMatchList(data[0].id)) {
              initIndex(data[0].id)
              Match(data[index].id)
            } else {
              Match(data[0].id)
            }
          }
        })
      }

      function initIndex (id) {
        // 头条推荐处理
        const index2 = rootState.home.Genre.findIndex(i => i.id !== id)
        index = index2 !== -1 ? index2 : 0
        // 头条推荐处理完成
      }
      function Match (id) {
        const selected = status < 2 ? '' : status - 2
        http.getBetHistory(id, selected, 1).then(resolve).catch((err) => {
          if (err.message) {
            try {
              const er = JSON.parse(err.message)
              if (er.code === 10001) router.replace({ path: '/login', query: { redirect: router.currentRoute.path } })
            } catch (e) {}
          }
        })
      }
    }).then(value => {
      if (typeof value === 'object' && value.length) {
        commit(SET_BET_HISTORY, {
          data: value,
          index: index,
          status: status,
          loadStatus: 1
        })
      }
      if (status !== state.BetStatus) {
        commit(SET_BET_STATUS, status)
      }
      if (index !== rootState.home.GenreIndex) {
        if (process.build.IF365) {
          commit(SET_365_GENRE_INDEX, index)
        } else {
          commit(SET_GENRE_INDEX, index)
        }
      }
    })
  },
  [UNBIND_WECHAT] ({ commit }, params = {}) {
    http.postUnbindWechat().then(data => {
      if (data) {
        commit(SET_WECHAT_BIND_STATUS, 0)
      }
      params.func()
    })
  },
  [BIND_WECHAT] ({ commit, state }, params = {}) {
    http.postBindWechat(params).then(data => {
      if (data) {
        commit(SET_WECHAT_BIND_STATUS, 1)
      }
    })
  },
  [WECHAT_LOGIN] ({ commit, state }, params = {}) {
    http.postWechatLogin(params).then(data => {
      if (data) {
        commit(SET_WECHAT_BIND_STATUS, 1)
      }
    })
  },
  [ADD_USER_BANK] ({ commit }, params = {}) {
    return http.postAddUserBank(params).then(data => {
      if (data) {
        commit(SET_USER_BANK, data)
      }
      return data
    })
  },
  [CHECK_USER_BANK] ({ commit }, params = {}) {
    http.poatCheckUserBank().then(data => {
      if (data) {
        commit(SET_USER_BANK, data)
        router.push({ name: params.path })
      }
    }).catch((response) => {
      router.push({ name: 'bindBank' })
    })
  },
  [WITH_DRAW] ({ commit }, params = {}) {
    return http.postWithDraw(params)
  },
  [GET_HISTORY] ({ commit, state }, params = {}) {
    let len = 0
    if (params.index === 1) {
      len = 1
      // state.History[params.type] = {}
    } else {
      len = state.History[params.type].length / 10 + 1
    }
    http.getUserMoneyHistory({ type: params.type, page: len }).then(data => {
      // if (data && data.length) {
      //   let obj = { data: data, index: params.index, type: params.type }
      //   commit(SET_HISTORY, obj)
      // }
      let obj = { data: data || [], index: params.index, type: params.type }
      commit(SET_HISTORY, obj)
      params.func()
    })
  },
  [ADD_PAYMENT_MSG] ({ commit }, params = {}) {
    http.postPaymentMsg(params).then(data => {
      router.go(-1)
      Toast('发送成功，客服会主动与您联系，请留意！')
    })
  },
  [GET_MONET_LOG] ({ commit, state }, params = {}) {
    let len = 0
    if (params.index === 1) {
      len = 1
      state.MoneyLog = {}
    } else {
      len = state.MoneyLog.length / 10 + 1
    }
    http.getUserMoneyHistory({ type: params.type, page: len }).then(data => {
      if (data && data.length) {
        let obj = { data: data, index: params.index }
        commit(SET_MONET_LOG, obj)
      }
      params.func()
    })
  },
  [GET_INVITE] ({ commit }) {
    return http.getInvite().then(data => {
      if (data) {
        commit(SET_INVITE_DATA, data)
      }
    })
  },
  [DELETE_COUPON] ({ commit }, params = {}) {
    http.postDeleteCoupon(params).then(data => {
      http.getCouponListByUid().then(data => {
        Toast('红包删除成功')
        if (data) {
          commit(SET_COUPON_LIST, data)
        }
      })
      http.getUserInfo().then(data => {
        if (data) {
          commit(SET_USER_INFO, data)
        }
      })
    })
  },
  [CHANGE_INVITED_STATUS] ({ commit }) {
    commit(SET_INVITED_COUPON, 0)
  },
  [GET_COUPON_LIST] ({ commit }) {
    return http.getCouponListByUid().then(data => {
      if (data) {
        commit(SET_COUPON_LIST, data)
      }
    })
  }
}
