<template>
  <div class="TheGameSwitch">
    <p
      v-for="(n,i) in Genre"
      :key="i"
      v-show="initMatch(n.id)"
      :class="{'opt-for':GenreIndex===i}"
      @touchmove="touchmove"
      @touchend="Switch(i,n)"
      @click="clickGo(i,n)"
    >{{n.game_name||'加载中..' }}</p>
  </div>
</template>

<script>//
import { mapState } from 'vuex'
import util, { initMatchList } from '../tool/util'

export default {
  name: 'TheGameSwitch',
  props: {
    ShowRecommended: { type: String } // 是否隐藏推荐,竞足...
  },
  data () {
    return { touchTig: false }
  },
  computed: {
    ...mapState({
      GenreIndex: state => state.home.GenreIndex,
      Genre: state => state.home.Genre
    })
  },
  methods: {
    touchmove () {
      this.touchTig === false && (this.touchTig = true)
    },
    Switch (index, item) {
      if (this.touchTig) {
        this.touchTig = false
        return
      }
      if ((item.id !== 0 && !item.id) || this.GenreIndex === index) return
      this.$emit('Switch', { index, type: 1 })
    },
    clickGo (i, n) {
      if (util.iFPhone) return
      this.Switch(i, n)
    },
    initMatch (id) {
      if (process.build.IF365) {
        return (this.ShowRecommended && initMatchList(id) === false) || !this.ShowRecommended
      } else {
        return (this.ShowRecommended && id !== 0) || !this.ShowRecommended
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .TheGameSwitch {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    overflow-x: scroll;
    overflow-y: hidden;
    @if $IF365 { background-color: $background-color; }
    @else { background-color: white; }
    p {
      line-height: 1;
      font-size: 15px;
      margin: 0 15px;
      padding: 13px 0;
      color: $font-color;
      white-space:nowrap;
      &.opt-for {
        color: $back-color;
        border-bottom: 2px solid $back-color;
      }
    }
  }
</style>
