<template>
  <div>
    <div class="score-title">
        <div class="top-nav">比分</div>
        <game-switching score @change="change"/>
      </div>
    <Loadmore :top-method="loadTop" ref="loadmore" spaceBetween="155">
      <ul class="score-body" :style="{minHeight: `${Device.height-155-49}px`}">
        <no-content
          tag="li"
          v-show="loading && !MatchSchedule.length"
          img="2"
          :text="['没有比赛信息!','去其它地方看看吧']"
        />
        <li v-for="(n,i) in MatchSchedule" :key="i">
          <div class="item-title-date">
            <div>
              <div>
                <span>{{ n.date|dateFormatDay('MM-dd',3651)}}</span>
                <span>{{n.match_num}}场比赛</span>
              </div>
            </div>
          </div>
          <ul style="margin: 0 13px;">
            <score-item
              v-for="nn in n.schedule_list"
              :key="`${nn.smid}${nn.smpid}`"
              :item="nn"
            />
          </ul>
        </li>
      </ul>
    </Loadmore>
  </div>
</template>

<script>//
import { mapActions, mapState } from 'vuex'
import { GET_365_HOME_DATA } from '../../../vuex/365/home'
import { GET_USER_INFO } from '../../../vuex/Mine'
import GameSwitching from '../../../components/365/GameSwitching'
import ScoreItem from '../../../components/365/ScoreItem'
import Loadmore from '../../../components/loadmore'

export default {
  name: 'score',
  components: {
    Loadmore,
    ScoreItem,
    GameSwitching
  },
  data () {
    return {
      nodeList: [],
      loading: false
    }
  },
  created () {
    const data = { type: 0 }
    if (this.MatchStatus < 2) data.index3 = 2
    this.getHomeData(data).finally(() => {
      this.loading = true
    })
  },
  mounted () {
    this.$nextTick(() => {
      let time = null
      window.onscroll = () => {
        // scrollTop就是触发滚轮事件时滚轮的高度
        clearTimeout(time)
        time = setTimeout(() => {
          !this.nodeList.length && (this.nodeList = document.querySelectorAll('.item-title-date'))
          const scrollTop = (document.documentElement.scrollTop || document.body.scrollTop) + 155
          let fixed = true
          for (let i = this.nodeList.length - 1; i >= 0; i--) {
            if (fixed && scrollTop > this.nodeList[i].offsetTop) {
              fixed = false
              this.nodeList[i].children[0].style = `position: fixed;z-index: 10;top: 155px;left:50%;transform: translateX(-50%);padding: 0 13px;`
            } else {
              this.nodeList[i].children[0].style = ''
            }
          }
        }, 150)
      }
    })
  },
  computed: {
    ...mapState({
      Device: state => state.mine.Device,
      MatchStatus: state => state.home.MatchStatus,
      MatchSchedule: state => {
        if (state.home.MatchStatus > 1 && state.home.MatchSchedule[state.home.PatternIndex] &&
          state.home.MatchSchedule[state.home.PatternIndex][state.home.GenreIndex] &&
          state.home.MatchSchedule[state.home.PatternIndex][state.home.GenreIndex][state.home.MatchStatus]
        ) {
          return state.home.MatchSchedule[state.home.PatternIndex][state.home.GenreIndex][state.home.MatchStatus]
        }
        return []
      }
    })
  },
  methods: {
    ...mapActions({
      getHomeData: GET_365_HOME_DATA,
      getUserInfo: GET_USER_INFO
    }),
    loadTop () {
      this.getHomeData({ type: 2 }).then(() => {
        setTimeout(() => {
          this.$refs.loadmore && this.$refs.loadmore.onTopLoaded()
        }, 500)
      })
    },
    change () {
      this.nodeList = []
    }
  }
}
</script>

<style lang="scss" scoped>
  .score-title {
      position: fixed;
      top: 0;
      width: 100%;
      max-width: 640px;
      background-color: #131313;
      margin: 0 auto;
      z-index: 2;
      .top-nav {
        height: 43px;
        line-height: 43px;
        color: $back-color;
        text-align: center;
        font-size: 15px;
        font-weight: bold;
      }
  }

  .score-body {
    > li > div.item-title-date {
        height: 33px;
        overflow: hidden;
        width: 100%;
        padding: 0 13px;

        > div {
          width: 100%;
          max-width: 640px;
          padding-top: 5px;

          > div {
            width: 100%;
            text-align: center;
            font-size: 12px;
            line-height: 28px;
            color: #ccc;
            border-radius: 4px;
            background-color: #313131;

            span:first-child {
              margin-right: 1em;
            }
          }
        }
      }
  }
</style>
