// http服务
import Axios from 'axios'
import loading from './loading'
import { Toast } from 'mint-ui'
import { token } from './storeManage'
import { bubbleSort } from './util'

class Provider {
  constructor (url) {
    // this.baseURL = url || process.build.IF365 ? 'http://d.d365dj.com/' : 'http://es.666esport.com/'
    // this.baseURL = url || process.build.IF365 ? 'http://172.16.3.48:84/' : 'http://es.666esport.com/'
    // this.baseURL = url || process.build.IF365 ? 'http://test.666esport.com/'/* 测试 */ : 'http://es.666esport.com/'
    // this.baseURL = url || process.build.IF365 ? 'http://www.dj3655.com/'/* 预发布 */ : 'http://es.666esport.com/'
    this.baseURL = url || process.build.IF365 ? 'http://www.d365dj.com/'/* 正式 */ : 'http://www.d365dj.com/'
    // this.headers = { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' }
    this.headers = { 'Content-Type': 'application/json; charset=utf-8' }
  }

  response (response) {
    // 响应
    return new Promise((resolve, reject) => {
      if (response.statusText === 'OK' && response.data && response.data.code === 1) {
        resolve(response.data.data)
      } else if (response.data && response.data.msg) {
        // 有错误code
        Toast(response.data.msg)
        if (response.data.code === 10001) {
          token.clearToken()
          reject(new Error(JSON.stringify({ data: response.data.data, code: 10001, msg: 'token失效' })))
        } else if (response.data.code === 40005) {
          reject(new Error(JSON.stringify({ data: response.data.data, code: 40005, msg: '请先前进行银行卡绑定操作!' })))
        }
      } else {
        Toast('网络错误')
      }
      reject(new Error(JSON.stringify({ data: response.data.data, msg: '未知错误' })))
      setTimeout(function () {
        loading.hide()
      }, 0)
    })
  }

  error (error) {
    if (error.response) {
      // 请求已发出，但服务器响应的状态码不在 2xx 范围内
      console.log(error.response.data)
      console.log(error.response.status)
      console.log(error.response.headers)
    } else {
      // Something happened in setting up the request that triggered an Error
      console.log('Error', error.message)
    }
    setTimeout(function () {
      loading.hide()
    }, 2000)
  }

  get (path = '', params = {}) {
    loading.show()
    const request = Axios({
      baseURL: this.baseURL,
      url: path,
      method: 'get',
      timeout: 10000,
      params,
      headers: this.headers
      // auth: this.auth
    })
    request.catch(this.error)
    return request.then(this.response)
  }

  post (path = '', data = {}, params = {}) {
    loading.show()
    const request = Axios({
      baseURL: this.baseURL,
      url: path,
      method: 'post',
      params,
      timeout: 10000,
      data,
      headers: this.headers,
      transformRequest: [function (data) {
        // 对 data 进行任意转换处理
        if (Object.prototype.toString.call(data) === '[object FormData]') {
          return data
        }
        const formData = new FormData()
        for (let i in data) {
          formData.append(i, data[i])
        }
        return formData
      }]
    })
    request.catch(this.error)
    return request.then(this.response)
  }
}

