<script>//
import { Button, Toast } from 'mint-ui'
import { mapActions, mapState } from 'vuex'
import { CHECK_USER_BANK, GET_USER_INFO, CHANGE_INVITED_STATUS } from '../../../vuex/Mine'
import { token } from '../../../tool/storeManage'
import InvitedSuccessPop from '../../../components/365/InvitedSuccessPop.vue'

export default {
  name: 'mine365',
  methods: {
    onClose () {
      this.changeInvitedStatus()
    },
    open () {
      if (!token.Token) {
        Toast('请登陆')
        return
      }
      this.$router.push({
        path: '/webPage',
        query: {
          url: `http://www.d365dj.com/api/user/chat?Token=${token.Token}&Device-Type=web`,
          title: '客服'
        }
      })
    },
    check (str) {
      this.checkUserBank({ path: str })
    },
    ...mapActions({
      getUserInfo: GET_USER_INFO,
      checkUserBank: CHECK_USER_BANK,
      changeInvitedStatus: CHANGE_INVITED_STATUS
    })
  },
  components: {
    InvitedSuccessPop, [Button.name]: Button
  },
  computed: {
    ...mapState({
      UserInfo: state => state.mine.User,
      invitedCoupon: state => state.mine.InvitedCoupon
    })
  },
  mounted () {
    if (JSON.stringify(this.UserInfo) === '{}') {
      this.getUserInfo().catch((msg) => {
        const message = JSON.parse(msg.message)
        if (message.code === 10001) {
          this.$router.replace({ path: '/login' })
        }
      })
    }
  }
}
</script>
