<template>
  <div class="game-wrapper" ref="wrapper">
    <ul class="content" :style="{width:`${clientWidth/4.5*(list.length||1)}px`}">
      <li
        v-for="n in list"
        :key="n.id"
        @click="$emit('Switch',n.index,n)"
        :class="{'grayscale':n.index===GenreIndex}"
      >
        <div>
          <span v-show="!icon&&n.match_mum&&MatchStatus">{{n.match_mum||0}}</span>
          <img v-lazy="n.icon" alt="">
        </div>
        <span>{{ n.game_name }}</span>
      </li>
    </ul>
  </div>
</template>

<script>//
import { mapState } from 'vuex'
import BScroll from 'better-scroll'

export default {
  name: 'GameList',
  props: {
    list: {
      type: Array,
      default () {
        return [{ game_name: '加载中...', icon: require('../../assets/home/ic_placepic@2x.png'), index: -1, id: -1 }]
      }
    },
    icon: { type: Boolean }
  },
  data () {
    return { scroll: false }
  },
  computed: {
    ...mapState({
      clientWidth: state => state.mine.Device.width,
      GenreIndex: state => state.home.GenreIndex,
      MatchStatus: state => !state.home.MatchStatus
    })
  },
  methods: {
    init (len) {
      if (len >= 5) {
        if (this.scroll) {
          this.scroll.refresh()
        } else {
          this.scroll = new BScroll(this.$refs.wrapper, {
            scrollX: true,
            scrollY: false,
            click: true
          })
        }
      } else {
        this.destroy()
      }
    },
    destroy () {
      if (this.scroll) {
        this.scroll.destroy()
        this.scroll = false
      }
    }
  },
  beforeDestroy () {
    this.destroy()
  },
  watch: {
    list (a) {
      this.init(a.length)
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.init(this.list.length)
    })
  }
}
</script>

<style lang="scss" scoped>
  .game-wrapper {
    width: 100%;
    height: 55px;
    background-color: #131313;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-touch-callout: none;
    user-select: none;

    ul.content {
      width: 100%;
      height: 100%;
      display: flex;
      flex-flow: row;
      justify-content: flex-start;

      > li {
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
        flex: 1 1;
        padding-top: 10px;

        > div {
          position: relative;

          > span {
            display: none;
            position: absolute;
            top: -10px;
            right: -1.5em;
            font-size: 12px;
            border-radius: 50%;
            text-align: center;
            line-height: 1.8em;
            width: 1.8em;
            height: 1.8em;
            color: white;
            background-color: #ff3333;
            z-index: -1;
            transform: scale(0.8);
          }

          > img {
            width: 26px;
            filter: grayscale(1);
          }

          img[lazy=loading] {
            height: 26px;
            background-image: url('../../assets/home/ic_placepic@2x.png');
            background-repeat: no-repeat;
            background-size: 100% 100%;
          }
        }

        > span {
          color: #999999;
        }
      }

      > li.grayscale {
        > div {
          > img {
            filter: none;
          }

          > span {
            display: block;
          }
        }

        > span {
          color: white;
        }
      }
    }

  }

</style>
