import Vue from 'vue'
import Vuex from 'vuex'
import { MineActions, MineMutations } from './Mine.js'

Vue.use(Vuex)
let STATUS = {}
if (process.build.IF365) {
  STATUS = {
    home: {
      state: {
        PassStatus: {// 投注数据
          show: false, // 悬浮窗
          odds: 1,
          UseRed: {
            // 选中的红包[id]:红包数据
          },
          Betting: {
            // 投注数据[smpid]:object
          },
          betLength: 0
        },
        PatternIndex: 0, // 过关 早盘
        Pattern: [],
        GenreIndex: 0, // 比赛类别
        Genre: [],
        MatchStatus: 0, // 0比赛赛程,1比赛赛果2比赛进行中3比赛已结束
        MatchSchedule: [] // 数据列表
      },
      mutations: require('./365/home').HomeMutations,
      actions: require('./365/home').HomeActions
    }
  }
} else {
  STATUS = {
    home: {
      state: {
        GenreIndex: 0,
        Genre: [{}], // 游戏类别菜单列表
        Banner: [[{}]], // 轮播图数据Banner[1]=英雄联盟
        MatchRecommend: [[]], // 推荐比赛
        MsgList: [[]]
      },
      mutations: require('./Home.js').HomeMutations,
      actions: require('./Home.js').HomeActions
    },
    shop: {
      state: {
        GoodsList: [], // 商品列表
        GoodsExchangeList: [] // 商品兑换消息列表
      },
      mutations: require('./Store.js').StoreMutations,
      actions: require('./Store.js').StoreActions
    },
    competition: {
      state: {
        MatchStatus: 1, // 1比赛赛程,2比赛赛果
        MatchSchedule: [] // 比赛列表[[[], []]]
      },
      mutations: require('./Competition.js').CompetitionMutations,
      actions: require('./Competition.js').CompetitionActions
    }
  }
}

export default new Vuex.Store({
  modules: {
    mine: {
      state: {
        Device: { // 设备信息
          navInfo: { wx: false, ios: false, android: false },
          width: 100,
          height: 100
        },
        User: {}, // 个人基本信息
        BingUserInfo: {}, // 个人账号绑定信息
        BetStatus: 1, // 竞猜状态：1全部, 2未结算（竞猜中）,3已结算
        BetHistory: [], // 竞猜历史
        AddressList: [], // 个人地址列表
        PropList: [], // 道具列表
        DiamondLog: [], // 钻石明细列表
        PropLog: [], // 道具明细列表,
        RechargeChannels: [], // 充值渠道列表
        UserBankInfo: {}, // 365 个人银行信息
        History: [[{}]], // 365 存取款历史记录
        MoneyLog: [], // 365 金额明细
        InvitedCoupon: 0, // 365 被邀请注册获得红包
        InviteData: [], // 365 邀请数据
        Coupon: [] // 365 红包列表
      },
      mutations: MineMutations,
      actions: MineActions
    },
    ...STATUS
  }
})
